// src/CandleChart.js

import React, { useEffect, useRef, useState } from "react";
import * as d3 from "d3";
import axios from "axios";

const CandleChart = () => {
  const svgRef = useRef();
  const [data, setData] = useState([]);
  const [interval, setInterval] = useState("minutes/1"); //  minutes/1 

  const intervals = {
    "1": "days",
    "1": "weeks",
    " ": "months",
    "1": "minutes/1",
    "3": "minutes/3",
    "5": "minutes/5",
    "10": "minutes/10",
    "15": "minutes/15",
    "30": "minutes/30",
    "1": "minutes/60",
    "4": "minutes/240",
  };

  useEffect(() => {
    const fetchData = (selectedInterval) => {
      const [type, value] = selectedInterval.split("/");
      const url =
        type === "minutes"
          ? `https://api.upbit.com/v1/candles/${type}/${value}?market=KRW-BTC&count=104`
          : `https://api.upbit.com/v1/candles/${type}?market=KRW-BTC&count=104`;

      axios
        .get(url)
        .then((response) => {
          const transformedData = response.data
            .map((d) => ({
              date: new Date(d.timestamp),
              open: d.opening_price,
              high: d.high_price,
              low: d.low_price,
              close: d.trade_price,
            }))
            .reverse(); //        
          setData(transformedData);
        })
        .catch((error) => console.error("Error fetching data: ", error));
    };

    fetchData(interval); //    interval     
  }, [interval]);

  useEffect(() => {
    if (data.length === 0) return;

    const svg = d3.select(svgRef.current);
    svg.selectAll("*").remove(); //   

    const width = 800;
    const height = 400;
    const margin = { top: 20, right: 80, bottom: 30, left: 30 }; //   80 

    // x y 
    const x = d3
      .scaleBand()
      .domain(data.map((d) => d.date))
      .range([margin.left, width - margin.right])
      .padding(0.3);

    const y = d3
      .scaleLinear()
      .domain([d3.min(data, (d) => d.low), d3.max(data, (d) => d.high)])
      .nice()
      .range([height - margin.bottom, margin.top]);

    const isMinutesInterval = interval.startsWith("minutes");

    const xAxis = (g) =>
      g
        .attr("transform", `translate(0,${height - margin.bottom})`)
        .call(
          d3
            .axisBottom(x)
            .tickFormat(d3.timeFormat(isMinutesInterval ? "%H:%M" : "%Y-%m-%d"))
            .tickValues(x.domain().filter((d, i) => i % 10 === 0))
        ) // 10  
        .call((g) => g.select(".domain").remove());

    const yAxis = (g) =>
      g
        .attr("transform", `translate(${width - margin.right},0)`)
        .call(d3.axisRight(y))
        .call((g) => g.select(".domain").remove())
        .call((g) => g.selectAll(".tick text").style("font-size", "12px").style("font-weight", "bold"));

    svg.append("g").call(xAxis);
    svg.append("g").call(yAxis);

    //   
    svg
      .append("g")
      .selectAll("rect")
      .data(data)
      .join("rect")
      .attr("x", (d) => x(d.date))
      .attr("y", (d) => y(Math.max(d.open, d.close)))
      .attr("height", (d) => Math.abs(y(d.open) - y(d.close)))
      .attr("width", x.bandwidth())
      .attr("fill", (d) => (d.close > d.open ? "red" : "blue")); // close  open   ,  

    svg
      .append("g")
      .selectAll("line")
      .data(data)
      .join("line")
      .attr("x1", (d) => x(d.date) + x.bandwidth() / 2)
      .attr("x2", (d) => x(d.date) + x.bandwidth() / 2)
      .attr("y1", (d) => y(d.high))
      .attr("y2", (d) => y(d.low))
      .attr("stroke", "black");
  }, [data, interval]);

  return (
    <div>
      <select onChange={(e) => setInterval(intervals[e.target.value])} defaultValue="1">
        <optgroup label=" ">
          <option value="1">1</option>
          <option value="1">1</option>
          <option value=" "> </option>
        </optgroup>
        <optgroup label=" ">
          {Object.keys(intervals)
            .filter((key) => key.includes("") || key.includes(""))
            .map((key) => (
              <option key={key} value={key}>
                {key}
              </option>
            ))}
        </optgroup>
      </select>
      <svg ref={svgRef} width={800} height={400}></svg>
    </div>
  );
};

export default CandleChart;
